import React, { useEffect, useState, useRef } from "react";
import { useSelector } from "react-redux";
import { Table, Button, Popconfirm, Space, message, Divider } from "antd";
import Page from "../../components/Page";
import dayjs from "dayjs";
import CreateAccount from "./components/CreateAccount.js";
import ListTable from "./components/ListTable";
import ModifyPassword from "./components/ModifyPassword";
import ModifyAccountRole from "./components/ModifyAccountRole";
import Frozen from "./components/Frozen";
import UnFrozen from "./components/UnFrozen";
import { accrount_create, password_update } from "../../api/acount";
import { role_get } from "../../api/role";
function Index(props) {
  const [data, setData] = useState([]);
  const childRef = useRef();
  const [visible, setVisible] = useState(false);
  const [add_visible, set_add_visible] = useState(false);
  const [password_visible, set_password_visible] = useState(false);
  const [accountRole_visible, set_accountRole_visible] = useState(false);
  const [fromData, setFromData] = useState([]);
  const [curAppid, setAppid] = useState();
  const [frozen_visible, set_frozen_visible] = useState();
  const [unforzen_visible, set_unforzen_visible] = useState();
  const [username, set_username] = useState("");
  const [id, set_id] = useState("");
  const [role_id, set_role_id] = useState("");
  const appidList = useSelector((state) => state.appids);
  const [role_data, set_role_data] = useState([]); //角色列表数据
  const edit = (record, index) => {
    let d = {
      ...record,
      time: [dayjs.unix(record.start_time), dayjs.unix(record.end_time)],
    };
    // console.log('编辑表单', d);
    setFromData(d);
    setVisible(true);
  };

  const onSubmit = async (fixData) => {};
  const add_onCancel = () => {
    set_add_visible(false);
  };
  const handleDelete = async (id) => {};

  useEffect(() => {}, [appidList]);
  //设置当前操作的用户名密码
  const set_opa_user = (username, id) => {
    set_username(username);
    set_id(id);
  };
  //创建账号
  const handleAdd = () => {
    set_add_visible(true);
  };

  //修改密码
  const modifypassword_h = (username, id) => {
    set_opa_user(username, id);
    set_password_visible(true);
  };

  //修改密码关闭
  const password_onCancel = () => {
    set_password_visible(false);
  };
  //修改权限
  const modifyaccountRole_h = (role_id, id) => {
    set_role_id(role_id);
    set_id(id);
    set_accountRole_visible(true);
  };
  //修改权限关闭
  const accountRole_onCancel = () => {
    set_accountRole_visible(false);
  };
  //冻结或解冻设置
  const block_h = (boolean, username, id) => {
    set_opa_user(username, id);
    boolean ? set_frozen_visible(true) : set_unforzen_visible(true);
  };
  //冻结关闭
  const frozen_onCancel = () => {
    set_frozen_visible(false);
  };
  //解冻关闭
  const unfrozen_onCancel = () => {
    set_unforzen_visible(false);
  };
  //新增账号确定
  const add_ok = () => {
    childRef.current.getData();
    set_add_visible(false);
  };
  //修改密码确定
  const password_update_ok = () => {
    set_password_visible(false);
    childRef.current.getData();
  };
  //获取角色
  const get_role = async () => {
    let data = await role_get({
      page: 1,
      page_size: 100000,
    });
    // console.log(data.data);
    set_role_data(data.data ? data.data : []);
  };
  //冻结成功
  const forzen_ok = () => {
    set_frozen_visible(false);
    childRef.current.getData();
  };
  //解冻成功
  const unforzen_ok = () => {
    // console.log(1234)
    set_unforzen_visible(false);
    childRef.current.getData();
  };
  //修改权限成功
  const accountRole_ok = () => {
    set_accountRole_visible(false);
    childRef.current.getData();
  };
  useEffect(() => {
    get_role();
  }, []);
  return (
    <Page title={props.route.title}>
      {/* <Space direction="vertical"> */}
      <Space>
        <Button type="primary" onClick={handleAdd}>
          创建账号
        </Button>
      </Space>
      <Divider dashed />
      <ListTable
        ref={childRef}
        modifypassword_h={modifypassword_h}
        modifyaccountRole_h={modifyaccountRole_h}
        block_h={block_h}
      />
      {/* </Space> */}
      <CreateAccount
        role_data={role_data}
        onOk={add_ok}
        visible={add_visible}
        onCancel={() => add_onCancel()}
      />
      <ModifyPassword
        username={username}
        id={id}
        onOk={password_update_ok}
        visible={password_visible}
        onCancel={() => password_onCancel()}
      />
      <ModifyAccountRole
        role_id={role_id}
        user_id={id}
        role_data={role_data}
        visible={accountRole_visible}
        onCancel={() => accountRole_onCancel()}
        onOk={() => accountRole_ok()}
      />
      <Frozen
        visible={frozen_visible}
        onCancel={() => frozen_onCancel()}
        username={username}
        id={id}
        onOk={() => forzen_ok()}
      />
      <UnFrozen
        visible={unforzen_visible}
        onCancel={() => unfrozen_onCancel()}
        username={username}
        id={id}
        onOk={() => unforzen_ok()}
      />
    </Page>
  );
}
export default Index;
