import React,{ memo, useState, useEffect } from "react";
import {
  Modal,
  Form,
  Input,
  Select,
  Button,
  Space,
  Col,
  Row,
  DatePicker,
  message,
} from "antd";
import dayjs from "dayjs";
import { upload_block } from "../../../api/acount";
const { RangePicker } = DatePicker;
const { Option } = Select;
const { TextArea } = Input;
const UnFrozen = (props) => {
  const { onCancel, visible, onOk, username, id } = props;
  const [form] = Form.useForm();
  //提交
  const submit = () => {
    try {
      form.validateFields().then(async () => {
        await upload_block({ ...form.getFieldsValue(), ban_time: "", id });
        message.success("解封成功");
        form.resetFields();
        onOk();
      });
    } catch (error) {
      console.log(error);
    }
  };
  const on_cancel = () => {
    form.resetFields();
    onCancel();
  };
  const FormConfig = {
    labelCol: { span: 6 },
    wrapperCol: { span: 18 },
  };
  useEffect(() => {
    if(visible){
      form.setFieldsValue({
        username,
      });
    }
  }, [visible]);
  return (
    <Modal title="解封" visible={visible} onCancel={on_cancel} onOk={submit}>
      <Form form={form} name="form_in_modal" {...FormConfig}>
        <Form.Item label="用户名" name="username">
          <Input disabled></Input>
        </Form.Item>
        {/* <Form.Item label="时间" name="istest">
          <DatePicker format="YYYY-MM-DD HH:mm:ss" showTime={{}} />
        </Form.Item> */}
        <Form.Item label="原因" name="ban_content">
          <TextArea placeholder="请输入原因"></TextArea>
        </Form.Item>
      </Form>
    </Modal>
  );
};
export default memo(UnFrozen);
