import React,{ memo, useState, useEffect } from "react";
import {
  Modal,
  Form,
  Input,
  Select,
  Button,
  Space,
  Row,
  Col,
  message,
} from "antd";
import { password_update } from "../../../api/acount";

const { Option } = Select;
const { TextArea } = Input;
const CreateAccount = (props) => {
  const { onCancel, visible, onOk, username, id } = props;
  const [form] = Form.useForm();
  //提交
  const submit = async () => {
    try {
      form.validateFields().then(async () => {
        await password_update({ ...form.getFieldsValue(), id });

        onOk();
        message.success("修改成功");
        form.resetFields();
      });
    } catch (e) {
      console.error(e);
    }
  };
  const on_cancel = () => {
    form.resetFields();
    onCancel();
  };
  const FormConfig = {
    labelCol: { span: 6 },
    wrapperCol: { span: 18 },
  };

  useEffect(() => {
    if(visible){
      form.setFieldsValue({
        name: username,
      });
    }
  }, [visible]);
  return (
    <Modal
      title="修改密码"
      visible={visible}
      onCancel={on_cancel}
      onOk={submit}
    >
      <Form
        {...FormConfig}
        form={form}
        name="form_in_modal"
        hideRequiredMark={true}
      >
        <Form.Item label="用户名" name="name">
          <Input disabled placeholder="请输入用户名" />
        </Form.Item>

        <Form.Item
          label="密码"
          name="pass_word"
          rules={[{ required: true, message: "请输入密码" }]}
        >
          <Input placeholder="请输入密码" />
        </Form.Item>

        <Form.Item
          label="再次输入密码"
          name="confirm_pass_word"
          rules={[
            { required: true, message: "请再次输入密码" },
            ({ getFieldValue }) => ({
              validator(_, value) {
                if (!value || getFieldValue("pass_word") === value) {
                  return Promise.resolve();
                }
                return Promise.reject(new Error("两次输入密码不一致"));
              },
            }),
          ]}
        >
          <Input placeholder="请再次输入密码" />
        </Form.Item>
      </Form>
    </Modal>
  );
};
export default memo(CreateAccount);
