import { Table, Button } from "antd";
import React, {
  useEffect,
  useState,
  forwardRef,
  useImperativeHandle,
} from "react";
import dayjs from "dayjs";
import { account_get, get_by_user_nam } from "../../../api/acount";
import { get_id_user, repacle_id_user_name } from "../../../utils";
import { get_user_name } from "../../../api/common";
// import mock from ''
const ListTable = (props, ref) => {
  useImperativeHandle(ref, () => ({
    // changeVal 就是暴露给父组件的方法
    getData: (newVal) => {
      account_get_handle(newVal);
    },
  }));
  const {
    modifypassword_h,
    modifyaccountRole_h,
    block_h,
    pageSize = 10,
    total = 0,
    showTotal = true,
  } = props;
  const [loading, setLoading] = useState(false);
  const [data, setData] = useState([]);
  const [pagination, setPagination] = useState({
    current: 1,
    pageSize: pageSize || 10,
    total: total || 0,
    showSizeChanger: true,
  });
  const columns = [
    { title: "ID", dataIndex: "id", key: "id" },
    { title: "用户名", dataIndex: "uname" },
    { title: "角色", dataIndex: "role_name" },
    {
      title: "状态",
      dataIndex: "state",
      render: (state) => (state == 1 ? "正常" : "冻结"),
    },
    {
      title: "最近登录时间",
      dataIndex: "last_time",
      render: (last_time) =>
        last_time ? dayjs(last_time).format("YYYY-MM-DD HH:mm:ss") : "",
    },
    {
      title: "创建时间",
      dataIndex: "created_at",
      render: (created_at) =>
        created_at ? dayjs(created_at).format("YYYY-MM-DD HH:mm:ss") : "",
    },
    {
      title: "创建者",
      dataIndex: "create_user_name",
    },
    {
      title: "操作",
      dataIndex: "operation",
      align: "center",
      render: (text, record, index) => {
        return (
          <span>
            <Button
              type="link"
              onClick={() => modifypassword_h(record.uname, record.id)}
            >
              修改密码
            </Button>
            <Button
              type="link"
              onClick={() => modifyaccountRole_h(record.role_id,record.id)}
            >
              修改权限
            </Button>
            <Button
              type="link"
              onClick={() =>
                block_h(Boolean(record.state == 1), record.uname, record.id)
              }
            >
              {/* 封停/解封 */}
              {record.state == 1 ? "封停" : "解封"}
            </Button>
          </span>
        );
      },
    },
  ];
  //获取账号列表
  const account_get_handle = async (params = {}) => {
    try {
      setLoading(true);
      let data = await account_get({
        page: pagination.current,
        page_size: pagination.pageSize,
        ...params,
      });

      if (data.data.length > 0) {
        const user_ids = get_id_user(data.data, { key: "create_user" });
        const ids_names_data = await get_by_user_nam({
          user_ids: user_ids.toString(),
        });
        if (ids_names_data) {
          let ids_names = ids_names_data.data;
          data.data = repacle_id_user_name(data.data, {
            key: "create_user",
            data: ids_names,
            trans_key: "create_user_name",
          });
        }
      }

      setData(data.data);
        setPagination({
        ...pagination,
        total: data.page.count,
      });
      // console.log(pagination);
      setLoading(false);
    } catch (e) {
      setLoading(false);
      console.error(e);
    }
  };
  //表格改变触发
  const onchange = (pagination) => {
    // console.log(pagination)
    setPagination({ ...pagination });
  };
  useEffect(() => {
    account_get_handle();
  }, [pagination.current,pagination.pageSize]);
  return (
    <>
      <Table
        bordered
        loading={loading}
        columns={columns}
        dataSource={data}
        rowKey="id"
        pagination={pagination}
        onChange={onchange}
      ></Table>
    </>
  );
};
export default forwardRef(ListTable);
