import axios from "utils/http";

//获取角色列表
export const role_get = (params) => {
  return axios.get(`/admin/role/gets`, params);
};
//删除角色
export const role_delete = (params) => {
  return axios.delete(`/admin/role/delete`, params);
};
//新增角色
export const role_create = (params) => {
  return axios.jpost(`/admin/role/create`, params);
};
//修改角色
export const role_update = (params) => {
  return axios.put(`/admin/role/update`, params);
};
//获取所有权限
export const permissions_get = (params) => {
  return axios.get(`/admin/permissions/gets`, params);
};
//获取所有权限
export const permissions_get_role = (params) => {
  return axios.get(`/admin/role/get-permissions`, params);
};
//获取角色下面的用户
export const get_role_by_user = (params) => {
  return axios.get(`/admin/role/get-role-by-user`, params);
};
