import React, { memo, useState, useEffect } from "react";
import {
  Modal,
  Form,
  Input,
  Select,
  Button,
  Space,
  Row,
  Col,
  Tree,
  message,
} from "antd";
import { permissions_get_role } from "../../../api/role";
import { trans_tree, trans_tree_get_key } from "../../../utils";
import { update_role } from "../../../api/acount";

const { Option } = Select;
const { TextArea } = Input;
const treeData = [
  {
    title: "0-0",
    key: "0-0",
    children: [
      {
        title: "0-0-0",
        key: "0-0-0",
        children: [
          {
            title: "0-0-0-0",
            key: "0-0-0-0",
          },
          {
            title: "0-0-0-1",
            key: "0-0-0-1",
          },
          {
            title: "0-0-0-2",
            key: "0-0-0-2",
          },
        ],
      },
      {
        title: "0-0-1",
        key: "0-0-1",
        children: [
          {
            title: "0-0-1-0",
            key: "0-0-1-0",
          },
          {
            title: "0-0-1-1",
            key: "0-0-1-1",
          },
          {
            title: "0-0-1-2",
            key: "0-0-1-2",
          },
        ],
      },
      {
        title: "0-0-2",
        key: "0-0-2",
      },
    ],
  },
  {
    title: "0-1",
    key: "0-1",
    children: [
      {
        title: "0-1-0-0",
        key: "0-1-0-0",
      },
      {
        title: "0-1-0-1",
        key: "0-1-0-1",
      },
      {
        title: "0-1-0-2",
        key: "0-1-0-2",
      },
    ],
  },
  {
    title: "0-2",
    key: "0-2",
  },
];
const CreateAccount = (props) => {
  const { onCancel, visible, onOk, role_data, role_id, user_id } = props;
  const [form] = Form.useForm();
  const [perms, set_perms] = useState();
  //提交
  const submit = () => {
    form.validateFields().then(async () => {
      await update_role({ ...form.getFieldsValue(), user_id });

      onOk();
      onCancel();
      message.success("修改成功");
    });
  };
  //取消
  const on_cancel = () => {
    form.resetFields();
    onCancel();
  };
  const FormConfig = {
    labelCol: { span: 6 },
    wrapperCol: { span: 18 },
  };

  const [expandedKeys, setExpandedKeys] = useState(["0-0-0", "0-0-1"]);
  const [checkedKeys, setCheckedKeys] = useState(["0-0-0"]);
  const [selectedKeys, setSelectedKeys] = useState([]);
  const [autoExpandParent, setAutoExpandParent] = useState(true);

  const onExpand = (expandedKeysValue) => {
    // console.log("onExpand", expandedKeysValue); // if not set autoExpandParent to false, if children expanded, parent can not collapse.
    // or, you can remove all expanded children keys.

    setExpandedKeys(expandedKeysValue);
    setAutoExpandParent(false);
  };

  const onCheck = (checkedKeysValue) => {
    console.log("onCheck", checkedKeysValue);
    setCheckedKeys(checkedKeysValue);
  };

  const onSelect = (selectedKeysValue, info) => {
    console.log("onSelect", info);
    setSelectedKeys(selectedKeysValue);
  };
  const permissions_get_role_h = async (id) => {
    try {
      let data = await permissions_get_role({ role_id: id });
      // console.log(data)
      let treeData = trans_tree(data.data, {
        key: "id",
        title: "name",
        children: "child",
      });
      set_perms(treeData);
      const keys = trans_tree_get_key(data.data, {
        key: "id",
        title: "name",
        children: "child",
        check: "check",
        checkState: true,
      });
      // console.log("测试", keys, data.data);
      setCheckedKeys(keys);
      // console.log(treeData)
      // set_perms(data.data);
    } catch (error) {
      console.error(error);
    }
  };
  useEffect(() => {
    if (visible) {
      permissions_get_role_h(role_id);
      form.setFieldsValue({
        role_id,
      });
    }
  }, [visible]);
  //选择角色
  const check_role = (id) => {
    // console.log(id);
    permissions_get_role_h(id);
  };
  return (
    <Modal
      title="修改权限"
      visible={visible}
      onOk={submit}
      onCancel={on_cancel}
    >
      <Form
        {...FormConfig}
        form={form}
        name="form_in_modal"
        hideRequiredMark={true}
      >
        <Form.Item label="角色" name="role_id">
          <Select style={{ width: "100%" }} onChange={check_role}>
            {role_data?.map((option) => (
              <Option key={option.id} value={option.id}>
                {option.name}
              </Option>
            ))}
          </Select>
        </Form.Item>

        <Form.Item label="权限" name="iscompulsory">
          <Tree
            disabled
            checkable
            onExpand={onExpand}
            expandedKeys={expandedKeys}
            autoExpandParent={autoExpandParent}
            onCheck={onCheck}
            checkedKeys={checkedKeys}
            onSelect={onSelect}
            selectedKeys={selectedKeys}
            treeData={perms}
          />
        </Form.Item>
        {/* <Row>
          <Col span={6}></Col>
          <Col span={18}>
            <Form.Item>
              <Space>
                <Button type="primary" htmlType="submit" onClick={submit}>
                  Submit
                </Button>
                <Button htmlType="button" onClick={onCancel}>
                  Cannel
                </Button>
              </Space>
            </Form.Item>
          </Col>
        </Row> */}
      </Form>
    </Modal>
  );
};
export default memo(CreateAccount);
