import React,{ memo, useState, useEffect } from "react";
import {
  Modal,
  Form,
  Input,
  Select,
  Button,
  Space,
  Row,
  Col,
  message,
} from "antd";
import { accrount_create } from "../../../api/acount";

const { Option } = Select;
const { TextArea } = Input;
const CreateAccount = (props) => {
  const { onCancel, visible, onOk, role_data } = props;
  const [form] = Form.useForm();
  //提交创建账号
  const submit = () => {
    try {
      form.validateFields().then(async () => {
        // console.log(form.getFieldsValue());
        await accrount_create(form.getFieldsValue());
        onOk();
        message.success("添加成功");
        form.resetFields();
      });
    } catch (err) {
      console.log(err);
    }
  };
  const on_cancel = () => {
    form.resetFields();
    onCancel();
  };
  const FormConfig = {
    labelCol: { span: 6 },
    wrapperCol: { span: 18 },
  };
  return (
    <Modal
      title="创建账号"
      visible={visible}
      onCancel={on_cancel}
      onOk={submit}
    >
      <Form
        {...FormConfig}
        form={form}
        name="form_in_modal"
        hideRequiredMark={true}
      >
        <Form.Item
          label="用户名"
          name="name"
          placeholder="请输入用户名"
          rules={[{ required: true, message: "请输入用户名" }]}
        >
          <Input placeholder="请输入用户名" />
        </Form.Item>

        <Form.Item
          label="密码"
          name="pass_word"
          rules={[{ required: true, message: "请输入密码" }]}
        >
          <Input placeholder="请输入密码" />
        </Form.Item>

        <Form.Item
          label="再次输入密码"
          name="confirm_pass_word"
          rules={[
            { required: true, message: "请再次输入密码" },
            ({ getFieldValue }) => ({
              validator(_, value) {
                if (!value || getFieldValue("pass_word") === value) {
                  return Promise.resolve();
                }
                return Promise.reject(new Error("两次输入密码不一致"));
              },
            }),
          ]}
        >
          <Input placeholder="请再次输入密码" />
        </Form.Item>

        <Form.Item
          label="角色"
          name="role_id"
          rules={[{ required: true, message: "请选择角色" }]}
        >
          <Select placeholder="请选择角色" style={{ width: "100%" }}>
            {role_data?.map((option) => (
              <Option key={option.id} value={option.id}>
                {option.name}
              </Option>
            ))}
          </Select>
        </Form.Item>

        <Form.Item
          label="备注"
          name="remarks"
          rules={[{ required: true, message: "请输入备注" }]}
        >
          <TextArea />
        </Form.Item>
      </Form>
    </Modal>
  );
};
export default memo(CreateAccount);
